import  React, { Component } from 'react';
import NavBar from './NavBar';
import axios from 'axios'

class Memories extends Component{
    constructor(props){
      super(props);
      this.state = {
        remember:'',
        photo:'',
        memories:[]
      };
    }

  handleChange = event =>{
    console.log(event.target.name)
    const {name, value} = event.target;

    this.setState({
      [name]:value
    })
  }
  fileSelectedHandler =(event)=>{
    console.log(event);


  }
  submitMemories =(event)=>{
    event.preventDefault();

    let currentMemories = this.state.memories
    let newMemory = {
      remember:this.state.remember,
      photo:this.state.photo,
    }
  
    axios('http://localhost:4000/memories', {
     method:'POST',
     data:newMemory
    })
    .then((result) => console.log(result))
    .catch((err)=>console.log(err))
    currentMemories.push(newMemory)
      this.setState({memories:currentMemories, remember:"", photo:""}, ()=>{
        console.log(this.state)
      })
  }
  render() {
    const { photo, remember } =this.state;
    let allMemories = this.state.memories.map(oneMemory =>{
      // debugger
      return(
        <div>
          <p>Memory:{oneMemory.remember}</p>
          <p>Photo:{oneMemory.photo}</p>
        </div>
      )
    })
    return(
      <div>
        <NavBar />
        {allMemories}
        <div id="form-main">
          <p className="container-msg">Write a message</p>
          <div id="form-div">
          <form className="form" id="form1" onSubmit={this.submitMemories}>
            <label>Memory</label>
            <input className ="feedback-input" type="text" name="remember" value={this.state.remember} onChange={this.handleChange} />
            <div className="text">
              <label for="comment">Photo</label>
              <input className="feedback-input" rows="5" id="comment" type="file" name="photo" value={this.state.photo} onChange={this.handleChange} />
            </div>
            <div className="submit-msg">
              <input type="submit" value="submit" id="button-blue"/>
            </div>
          </form>
          </div>
        </div>
      </div>
    );
  }
}
export default Memories;


